<template>
    <b-container fluid class="home">
        <b-row>
            <h1>{{ title }}</h1>
        </b-row>
        <b-row>
            <h2><i>{{ subtitle }}</i></h2>
        </b-row>
        <hr />
        <b-row>
            <b-col></b-col>
            <b-col>
                <b-embed type="video" aspect="16by9" loop muted autoplay>
                    <source src="../assets/clips/tower.mp4" type="video/mp4">
                </b-embed>
            </b-col>
            <b-col></b-col>
        </b-row>
        <hr />
        <b-row>
            <b-button ref="btnStart" block squared variant="success" @click="toggleServer" :pressed.sync="serverToggle">Start Server</b-button>
            <b-button ref="btnStop" hidden block squared variant="danger" @click="toggleServer" :pressed.sync="serverToggle">Stop Server</b-button>
        </b-row>
        <hr />
        <b-row>
            <h3>Server Log</h3>
        </b-row>
        <b-row>
            <textarea class="log"
                      block
                      ref="txtServerLog"
                      no-resize
                      rows="4"
                      :value="serverLog"></textarea>
        </b-row>
        <b-row>
            <b-button ref="btnClearLog" squared variant="primary" @click="clearLog">Clear Log</b-button>
        </b-row>
    </b-container>
</template>

<script>
    export default {
        name: 'Home',
        props: {
            title: String,
            subtitle: String
        },
        data: function () {
            return {
                serverToggle: false,
                serverLog: ""
            }
        },
        methods: {
            toggleServer() {
                if (this.serverToggle) {
                    this.serverLog += '^ Started: ' + new Date().toLocaleString() + '\n';
                } else {
                    this.serverLog += 'v Stopped: ' + new Date().toLocaleString() + '\n';
                }

                this.$refs.btnStart.hidden = !this.$refs.btnStart.hidden;
                this.$refs.btnStop.hidden = !this.$refs.btnStop.hidden;
            },
            clearLog() {
                this.serverLog = "";
            }
        }
    };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .log {
        background: #0a0a0a;
        color: #ffffff;
        width: 100%;
    }
</style>

